.navbar{
    display: flex;
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 70px;
    justify-content: space-between;
    background: transparent;
    top: 0;
    
    /* background-color: #283747; */
}
.navbar a{
    text-decoration: none;
    font-size: 25px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: whitesmoke;
    
    padding: 10px;
    height: 60px;
    
}
.navbar div{
    display: flex;
    gap: 30px;
    height: 60px;
    text-align: center;
    align-items: center;
}
.navbar div a:hover{background-color: whitesmoke;color: #283747;}

